import { Action } from '@ngrx/store'

export const SET_SLIDEOUT_VISIBILITY = '[APP] Set Sidebar Visibilty';
export const GET_RESTAURANTS = '[APP] Get Restaurants';
export const GET_RESTAURANTS_REQUEST = '[APP] Get Restaurants Request';
export const GET_RESTAURANTS_SUCCESS = '[APP] Get Restaurants Success';
export const GET_RESTAURANTS_ERROR = '[APP] Get Restaurants Error';
export const UPDATE_LOCATION = '[APP] Update Location';

export const SET_LOADING = '[APP] Set Loading';

export class SetSlideoutVisibility implements Action {
    readonly type = SET_SLIDEOUT_VISIBILITY

    //We will pass the bool + cardID
    constructor(public payload: any) {}
}

//Dispatches to display our loading screen
export class SetLoading implements Action {
    readonly type = SET_LOADING

    //We will pass whether or not to show loading screens
    constructor(public payload: boolean) {}
}

//Dispatches our effect
export class GetRestaurants implements Action {
    readonly type = GET_RESTAURANTS
}

//Returned if we successfully get restaurants
export class GetRestaurantsSuccess implements Action{
    readonly type = GET_RESTAURANTS_SUCCESS

    // Used any here. 
    // Tried to use a Restaurants interface but not sure where to refactor it all at
    constructor(public payload: any){}
}

//Used to update our coordinates within our state
export class UpdateLocation implements Action{
    readonly type = UPDATE_LOCATION

    constructor(public payload: Object){}
}

//Returned if we end up with an error
export class GetRestaurantsError implements Action{
    readonly type = GET_RESTAURANTS_ERROR;
    constructor(public payload: string){}
}

export type Actions = 
                    SetSlideoutVisibility | 
                    GetRestaurants |
                    GetRestaurantsSuccess |
                    GetRestaurantsError |
                    UpdateLocation |
                    SetLoading