import { Component, OnInit } from '@angular/core';
import { Observable, Subscription } from 'rxjs';
import { Store } from '@ngrx/store';
import { App } from '../models/app.model';
import { AppState } from '../app.state';

@Component({
  selector: 'app-info-screen',
  templateUrl: './info-screen.component.html',
  styleUrls: ['./info-screen.component.css']
})
export class InfoScreenComponent implements OnInit {
  private subscription: Subscription;

  title = 'angular-lunch-tyme';
  stateObservable: Observable<App>;
  state: AppState;

  constructor(private store: Store<AppState>) { 

    this.stateObservable = this.store.select('app');
    this.subscription = this.stateObservable.pipe().subscribe(
      (data:any) => {
        this.state = data;
      });
  }

  ngOnInit() {
  }

  //clean up our subscription
  ngOnDestroy()
  {
    this.subscription.unsubscribe();
  }

}
