import { Observable, Subscription } from 'rxjs';
import { Store } from '@ngrx/store';
import { App } from './models/app.model';
import { AppState } from './app.state';

import { Component } from '@angular/core';
import * as AppActions from './actions/app.actions';


@Component({
  selector: 'app-root',
  templateUrl: './app.component.html',
  styleUrls: ['./app.component.css']
})
export class AppComponent {
  private subscription: Subscription;

  title = 'angular-lunch-tyme';
  stateObservable: Observable<App>;
  state: AppState;

  constructor(private store: Store<AppState>) { 

    this.stateObservable = this.store.select('app');
    this.subscription = this.stateObservable.pipe().subscribe(
      (data:any) => {
        this.state = data;
      });
  }

  //Function to dispatch and action and close our details panel.
  closeDetails()
  {
    console.log('close our details');
    this.store.dispatch(new AppActions.SetSlideoutVisibility({newVisibility:false, cardId: 0}));

  }

  //If location succeeds, store location data and get restaurants
  fetchLocation()
  {
    //dispatch our loading screen
    this.store.dispatch(new AppActions.SetLoading(true));

    //Notify onclick
    console.log('fetch location');

    // We need to display our spinner here to demonstrate loading
    // The Location API takes ages for some reason.
    
    if(navigator.geolocation)
    {
      navigator.geolocation.getCurrentPosition(position => {
        console.log('get position success');
        console.log(position.coords);

        //Note: Technically this should refactor into 1 dispatch

        //Dispatch store our location
        this.store.dispatch(new AppActions.UpdateLocation(position.coords));
        
        //Dispatch get restaurants
        console.log('Dispatching getRestaurants');
        this.store.dispatch(new AppActions.GetRestaurants());
      },
      error => {
        console.log(error);
        this.store.dispatch(new AppActions.GetRestaurantsError(error.message));
      },
      {timeout:10000});
   }
  }

  ngOnInit() {
    
  }

  //clean up our subscription
  ngOnDestroy()
  {
    this.subscription.unsubscribe();
  }
}
