import { Injectable, Inject } from '@angular/core';

import {Effect, Actions, ofType} from '@ngrx/effects';
import { map, switchMap, catchError } from 'rxjs/operators';
import { of } from 'rxjs';

import * as AppActions from '../actions/app.actions';
import {AppService} from '../app.service';

@Injectable()
export class AppEffects{
    constructor(private actions$: Actions,
        private appService: AppService){}

    @Effect()
    getRestaurants$ = this.actions$.pipe(ofType(AppActions.GET_RESTAURANTS),
        //switchMap is blocking and cancels all requests when called
        switchMap(() =>{
            return this.appService.getRestaurants().pipe(
                map(response => new AppActions.GetRestaurantsSuccess(response)),
                catchError(error => of(new AppActions.GetRestaurantsError(error)))
            )
        })

    );
}


export const effects: any[] = [AppEffects];