import { Component, OnInit } from '@angular/core';
import { Observable, Subscription } from 'rxjs';
import { Store } from '@ngrx/store';
import { App } from '../models/app.model';
import { AppState } from '../app.state';

@Component({
  selector: 'app-restaurant-details',
  templateUrl: './restaurant-details.component.html',
  styleUrls: ['./restaurant-details.component.css']
})
export class RestaurantDetailsComponent implements OnInit {

  private subscription: Subscription;

  stateObservable: Observable<App>;

  //I am still not sure if I should be fetching entire state
  state: AppState;

  //Note: I honestly tried 20 ways to use the | async, but this just works.
  constructor(private store: Store<AppState>) {
    this.stateObservable = this.store.select('app');
    this.subscription = this.stateObservable.pipe().subscribe(
      (data:any) => {
        this.state = data;
      });

      console.log('component state');
      console.log(this.state);
  }


  ngOnInit() {
  }

  //clean up our subscription
  ngOnDestroy()
  {
    this.subscription.unsubscribe();
  }

}
